/* eslint-disable import/prefer-default-export */
import { getThemeAPIKeyFromName } from '../dataUtils';
import { TILE_TOKEN_NAMES } from '../../const/tokens/tileTokens';
import { TILE_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/tileTokenDefaults';
// import { CORE_COLOR_NEUTRAL_DEFAULTS } from '../../const/colorNeutralTokenDefaultsConst';

const {
    TILE_BORDER_RADIUS,
    TILE_HEADING_BORDER_WIDTH,
    TILE_HEADING_FONT_WEIGHT,
} = TILE_TOKEN_NAMES;

export const getTileStyles = (data = {}) => {
    const apiKeyBorderRadius = getThemeAPIKeyFromName(TILE_BORDER_RADIUS);
    const apiKeyHeadingBorderBottomWidth = getThemeAPIKeyFromName(
        TILE_HEADING_BORDER_WIDTH,
    );
    const apiKeyHeadingFontWeight = getThemeAPIKeyFromName(
        TILE_HEADING_FONT_WEIGHT,
    );
    const defaultBorderRadius = TILE_STYLE_TOKEN_DEFAULTS[TILE_BORDER_RADIUS];
    const defaultHeadingBorderBottomWidth =
        TILE_STYLE_TOKEN_DEFAULTS[TILE_HEADING_BORDER_WIDTH];
    const defaultHeadingFontWeight =
        TILE_STYLE_TOKEN_DEFAULTS[TILE_HEADING_FONT_WEIGHT];
    const {
        [apiKeyBorderRadius]: borderRadius = defaultBorderRadius,
        [apiKeyHeadingBorderBottomWidth]:
            headingBorderBottomWidth = defaultHeadingBorderBottomWidth,
        [apiKeyHeadingFontWeight]: headingFontWeight = defaultHeadingFontWeight,
    } = data;

    return {
        [apiKeyBorderRadius]: borderRadius,
        [apiKeyHeadingBorderBottomWidth]: headingBorderBottomWidth,
        [apiKeyHeadingFontWeight]: headingFontWeight,
    };
};
