/* eslint-disable import/prefer-default-export */
import { getThemeAPIKeyFromName } from '../dataUtils';
import { LIST_TOKEN_NAMES } from '../../const/tokens/listTokens';
import { LIST_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/listTokenDefaults';

const { LIST_BORDER_WIDTH, LIST_ITEM_ICON_COLOR } = LIST_TOKEN_NAMES;

export const getListStyles = (data = {}) => {
    const apiKeyBorderWidth = getThemeAPIKeyFromName(LIST_BORDER_WIDTH);
    const defaultBorderWidth = LIST_STYLE_TOKEN_DEFAULTS[LIST_BORDER_WIDTH];
    const { [apiKeyBorderWidth]: borderWidth = defaultBorderWidth } = data;

    return {
        [apiKeyBorderWidth]: borderWidth,
    };
};

export const getListTheme = (colors = {}, config = {}) => {
    const { sourceColors = {} } = colors;
    const { shouldAutoCorrectColors = true } = config;
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;

    return {
        [getThemeAPIKeyFromName(LIST_ITEM_ICON_COLOR)]: shouldAutoCorrectColors
            ? recommendedNonTextColor.hex
            : base.hex,
    };
};
