/* eslint-disable import/prefer-default-export */
import {
    getNewColorByChangingLightness,
    evaluateColorCompatibilities,
} from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getThemeAPIKeyFromName } from '../dataUtils';
import { AVATAR_TOKEN_NAMES } from '../../const/tokens/avatarTokens';

const { AVATAR_COLOR_BG, AVATAR_COLOR_TEXT } = AVATAR_TOKEN_NAMES;

export const getAvatarTheme = (colors = {}, config = {}) => {
    const { sourceColors = {}, canvasColor = {} } = colors;
    const { base = {}, variants = [] } = sourceColors;
    const { isDark } = canvasColor;
    const { isDarkMode, shouldAutoCorrectColors } = config;
    let textColor = base.hex;
    const bgLightness = isDarkMode ? 20 : 80;
    const bgColor = getNewColorByChangingLightness(
        canvasColor.hex,
        bgLightness,
    );
    const ops = [base, ...variants];
    const { compatibleTextColors } = evaluateColorCompatibilities(
        { hex: bgColor },
        ops,
    );
    const canUseBase = compatibleTextColors.indexOf('base') > -1;

    if (!canUseBase) {
        const mathMethod = isDark ? Math.min : Math.max;
        const compatibleOps = ops.filter(
            (op) => compatibleTextColors.indexOf(op.id) > -1,
        );
        const compatibleOpsLightnesses = compatibleOps.map(
            (op) => op.lightness,
        );
        const opLightness = mathMethod(...compatibleOpsLightnesses);
        const compatibleOp = ops.find((op) => op.lightness === opLightness);

        if (shouldAutoCorrectColors) {
            textColor = compatibleOp?.hex ?? textColor;
        }
    }

    return {
        [getThemeAPIKeyFromName(AVATAR_COLOR_BG)]: bgColor,
        [getThemeAPIKeyFromName(AVATAR_COLOR_TEXT)]: textColor,
    };
};
