import {
    convertColorToRGBA,
    convertColorToHex,
} from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getThemeAPIKeyFromName } from '../dataUtils';
import { TOASTER_TOKEN_NAMES } from '../../const/tokens/toasterTokens';
import { TOASTER_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/toasterTokenDefaults';
// import { CORE_COLOR_NEUTRAL_DEFAULTS } from '../../const/colorNeutralTokenDefaultsConst';
import { CORE_NEUTRAL_COLOR_TOKEN_NAMES } from '../../const/tokens/coreTokens';
import { CORE_COLOR_NEUTRAL_TOKEN_DEFAULTS } from '../../const/token-defaults/coreTokenDefaults';

const {
    TOASTER_PROGRESS_BAR_COLOR,
    TOASTER_ERROR_COLOR_ICON,
    TOASTER_INFO_COLOR_ICON,
    TOASTER_SUCCESS_COLOR_ICON,
    TOASTER_WARNING_COLOR_ICON,
    TOASTER_BORDER_RADIUS,
} = TOASTER_TOKEN_NAMES;
const { CORE_COLOR_NEUTRAL_BLACK, CORE_COLOR_NEUTRAL_WHITE } =
    CORE_NEUTRAL_COLOR_TOKEN_NAMES;

const getToasterTheme = ({
    sourceColors,
    shouldAutoCorrectColors = true,
    tokenId,
}) => {
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;

    return {
        [getThemeAPIKeyFromName(tokenId)]: shouldAutoCorrectColors
            ? recommendedNonTextColor.hex
            : base.hex,
    };
};

export const getToasterStyles = (data = {}) => {
    const apiKeyBorderRadius = getThemeAPIKeyFromName(TOASTER_BORDER_RADIUS);
    const defaultBorderRadius =
        TOASTER_STYLE_TOKEN_DEFAULTS[TOASTER_BORDER_RADIUS];

    const { [apiKeyBorderRadius]: borderRadius = defaultBorderRadius } = data;

    return {
        [apiKeyBorderRadius]: borderRadius,
    };
};

export const getToasterAffirmativeTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;

    return getToasterTheme({
        sourceColors,
        ...config,
        tokenId: TOASTER_SUCCESS_COLOR_ICON,
    });
};

export const getToasterCautionaryTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;

    return getToasterTheme({
        sourceColors,
        ...config,
        tokenId: TOASTER_WARNING_COLOR_ICON,
    });
};

export const getToasterErrorTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;

    return getToasterTheme({
        sourceColors,
        ...config,
        tokenId: TOASTER_ERROR_COLOR_ICON,
    });
};

export const getToasterInfoTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;

    return getToasterTheme({
        sourceColors,
        ...config,
        tokenId: TOASTER_INFO_COLOR_ICON,
    });
};

export const getToasterDefaultTheme = (config = {}) => {
    const { isDarkMode, isMobile } = config;
    const rgba = convertColorToRGBA(
        isDarkMode
            ? CORE_COLOR_NEUTRAL_TOKEN_DEFAULTS[CORE_COLOR_NEUTRAL_WHITE]
            : CORE_COLOR_NEUTRAL_TOKEN_DEFAULTS[CORE_COLOR_NEUTRAL_BLACK],
        0.3,
        true,
    );

    return {
        [getThemeAPIKeyFromName(TOASTER_PROGRESS_BAR_COLOR)]: isMobile
            ? convertColorToHex(rgba)
            : rgba,
    };
};
