import PropTypes from 'prop-types';
import cx from 'classnames';

// Styles
import './styles/resource-list-item-icon.scss';

const ResourceListItemIcon = (props) => {
    const { contextCls, shouldUseColor, renderIcon } = props;
    const baseCls = 'eureka-resource-list-item-icon';
    const modCls = {
        [`${baseCls}--uses-color`]: shouldUseColor,
    };

    return (
        <span className={cx(baseCls, modCls, contextCls)}>
            <span className={`${baseCls}__icon-safe-space`}>
                {renderIcon && renderIcon(`${baseCls}__icon`)}
            </span>
        </span>
    );
};

ResourceListItemIcon.propTypes = {
    contextCls: PropTypes.string,
    shouldUseColor: PropTypes.bool,
    renderIcon: PropTypes.func,
};

export default ResourceListItemIcon;
