export const getBaseFontSize = () => 15;

export const getFallbackFontFamily = () => 'sans-serif';

export const getFontSizeScale = () => 1.125;

export const getTypographyFontSizes = (
    baseFontSize = getBaseFontSize(),
    fontSizeScale = getFontSizeScale(),
) => {
    const subText = Math.round(baseFontSize / fontSizeScale);
    const footnote = Math.round(baseFontSize / fontSizeScale ** 2);
    // Large font size (bodyLargeText) is used for forms and
    // cannot go below 16 due to accessibility requirements
    const bodyLargeText = Math.max(
        Math.round(baseFontSize * fontSizeScale),
        16,
    );
    const h5 = Math.round(baseFontSize * fontSizeScale ** 2);
    const h4 = Math.round(baseFontSize * fontSizeScale ** 3);
    const h3 = Math.round(baseFontSize * fontSizeScale ** 4);
    const h2 = Math.round(baseFontSize * fontSizeScale ** 5);
    const h1 = Math.round(baseFontSize * fontSizeScale ** 6);
    const numberMedium = Math.round(baseFontSize * fontSizeScale ** 7);
    const numberSmall = Math.round(baseFontSize * fontSizeScale ** 4);

    return {
        baseSize: baseFontSize,
        bodyLargeText,
        footnote,
        h1,
        h2,
        h3,
        h4,
        h5,
        numberMedium,
        numberSmall,
        subText,
    };
};
