import i18next from 'i18next';
import {
    initReactI18next,
    useTranslation as i18nUseTranslation,
    withTranslation as withTranslationI18N,
} from 'react-i18next';
import PropTypes from 'prop-types';
import Backend from './Backend';

const LOCAL_STORAGE_KEY = 'language';

/**
 * Get translated string for the given key and options
 *
 * @param {string} key translation key (for example, 'common.search')
 * @param  {...any} rest options for i18next's 't' function
 * @see https://www.i18next.com/overview/api#t
 * @returns {string} translated string
 */
export function getTranslation(key, ...rest) {
    return i18next.t(key, ...rest);
}

/**
 * HOC for class component to inject i18n props
 *
 * @param {Component} Component react component to be wrapped
 * @returns {Component} wrapped component
 */
export const withTranslation = (Component) => {
    const Wrapper = ({ t, tReady, ...rest }) => {
        const compProps = {
            ...rest,
            i18n: { getTranslation: t },
            isI18nReady: tReady,
        };

        return <Component {...compProps} />;
    };
    Wrapper.propTypes = {
        t: PropTypes.func,
        tReady: PropTypes.bool, // eslint-disable-line react/boolean-prop-naming
    };

    return withTranslationI18N()(Wrapper);
};

/**
 * useTranslation hook for functional components
 *
 * @returns {Object} { getTranslation } object
 */
export function useTranslation() {
    const { t, ready, ...rest } = i18nUseTranslation();

    return {
        getTranslation: t,
        isI18nReady: ready,
        ...rest,
    };
}

/**
 * Get currently selected language or default language
 *
 * @returns {string} language (for example, 'en-US')
 */
export function getLanguage() {
    return window.localStorage.getItem(LOCAL_STORAGE_KEY) || i18next.language;
}

/**
 * Set new language
 *
 * @param {string} newLanguage language (for example, 'en-US')
 *  @param {Object} options
 * @param {options.language} [newLanguage] language to update translations for
 * @param {options.strings} [options={}] object containing strings values
 */
export function setLanguage(language = 'en-US') {
    if (getLanguage() !== language) {
        window.localStorage.setItem(LOCAL_STORAGE_KEY, language);

        return i18next.changeLanguage(language);
    }

    return Promise.resolve();
}

/**
 * Initialize translation
 *
 * @param {Object} options Options
 * @param {string} [options.fallbackLng='en-US'] Fallback language. Used when the app could not load default or previously selected language.
 * @param {Object} [options.defaultTranslations] Object containing fallback strings for all languages in the form of {<locale>: Promise returning strings object}.
 * @param {string} [options.loadPath] Path to API endpoint returning translation strings.
 * @param {string} [options.partnerId] partnerId for which overriden translations need to be loaded. this is used to construct the loadPath if loadPath is not provided
 * @param {boolean} [options.shouldLoadStrings] Defines if initTranslations should make an attempt to load strings from loadPath location.
 */
export function initTranslations({
    fallbackLng = 'en-US',
    defaultTranslations,
    loadPath,
    shouldLoadStrings = true,
    partnerId,
} = {}) {
    return i18next
        .use(initReactI18next)
        .use(Backend)
        .init({
            fallbackLng,
            interpolation: {
                escapeValue: false,
            },
            lng: getLanguage(),
            load: 'currentOnly',
            ns: [],
            partialBundledLanguages: true,
            backend: {
                loadPath,
                defaultTranslations,
                shouldLoadStrings,
                partnerId,
            },
            react: {
                useSuspense: false,
            },
        });
}
