import { CORE_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/coreTokenDefaults';

const {
    BTN_PRIMARY_COLOR_BG,
    BTN_PRIMARY_COLOR_BORDER,
    BTN_PRIMARY_COLOR_TEXT,
    BTN_PRIMARY_ACTIVE_COLOR_BG,
    BTN_PRIMARY_ACTIVE_COLOR_BORDER,
    BTN_PRIMARY_ACTIVE_COLOR_TEXT,
    BTN_PRIMARY_HOVER_COLOR_BG,
    BTN_PRIMARY_HOVER_COLOR_BORDER,
    BTN_PRIMARY_HOVER_COLOR_TEXT,
    BTN_PRIMARY_FOCUS_COLOR_HALO,
    BTN_SPLIT_PRIMARY_COLOR_BORDER,
    BTN_PRIMARY_DESTRUCTIVE_COLOR_BG,
    BTN_PRIMARY_DESTRUCTIVE_COLOR_BORDER,
    BTN_PRIMARY_DESTRUCTIVE_COLOR_TEXT,
    BTN_PRIMARY_DESTRUCTIVE_ACTIVE_COLOR_BG,
    BTN_PRIMARY_DESTRUCTIVE_ACTIVE_COLOR_BORDER,
    BTN_PRIMARY_DESTRUCTIVE_ACTIVE_COLOR_TEXT,
    BTN_PRIMARY_DESTRUCTIVE_HOVER_COLOR_BG,
    BTN_PRIMARY_DESTRUCTIVE_HOVER_COLOR_BORDER,
    BTN_PRIMARY_DESTRUCTIVE_HOVER_COLOR_TEXT,
    BTN_PRIMARY_DESTRUCTIVE_FOCUS_COLOR_HALO,
    BTN_SPLIT_PRIMARY_DESTRUCTIVE_COLOR_BORDER,
    BTN_SECONDARY_COLOR_BG,
    BTN_SECONDARY_COLOR_BORDER,
    BTN_SECONDARY_COLOR_TEXT,
    BTN_SECONDARY_ACTIVE_COLOR_BG,
    BTN_SECONDARY_ACTIVE_COLOR_BORDER,
    BTN_SECONDARY_ACTIVE_COLOR_TEXT,
    BTN_SECONDARY_HOVER_COLOR_BG,
    BTN_SECONDARY_HOVER_COLOR_BORDER,
    BTN_SECONDARY_HOVER_COLOR_TEXT,
    BTN_SECONDARY_FOCUS_COLOR_HALO,
    BTN_SPLIT_SECONDARY_COLOR_BORDER,
    BTN_SECONDARY_DESTRUCTIVE_COLOR_BG,
    BTN_SECONDARY_DESTRUCTIVE_COLOR_BORDER,
    BTN_SECONDARY_DESTRUCTIVE_COLOR_TEXT,
    BTN_SECONDARY_DESTRUCTIVE_FOCUS_COLOR_HALO,
    BTN_SECONDARY_DESTRUCTIVE_ACTIVE_COLOR_BG,
    BTN_SECONDARY_DESTRUCTIVE_ACTIVE_COLOR_BORDER,
    BTN_SECONDARY_DESTRUCTIVE_ACTIVE_COLOR_TEXT,
    BTN_SECONDARY_DESTRUCTIVE_HOVER_COLOR_BG,
    BTN_SECONDARY_DESTRUCTIVE_HOVER_COLOR_BORDER,
    BTN_SECONDARY_DESTRUCTIVE_HOVER_COLOR_TEXT,
    BTN_SPLIT_SECONDARY_DESTRUCTIVE_COLOR_BORDER,
    BTN_BORDER_RADIUS,
    BTN_BORDER_WIDTH,
    BTN_FONT_WEIGHT,
    DIALOG_ACTION_ACTIVE_COLOR_BG,
    DIALOG_ACTION_ACTIVE_COLOR_ICON,
    DIALOG_ACTION_BORDER_RADIUS,
    DIALOG_ACTION_COLOR_BG,
    DIALOG_ACTION_COLOR_ICON,
    DIALOG_ACTION_HOVER_COLOR_BG,
    DIALOG_ACTION_HOVER_COLOR_ICON,
} = BTN_TOKEN_NAMES;
const { CORE_SYSTEM_SCALE } = CORE_STYLE_TOKEN_DEFAULTS;

const getPrimaryBtnHoverBGColor = (sourceColors = {}) => {
    const { base = {}, variants = [] } = sourceColors;
    const { hex, isDark } = base;
    const baseColorLightness = getColorLightness(hex);
    const variantsLightnessArr = variants.map((variant) => variant?.lightness);
    const variantA = variants[0] ?? {};
    const variantB = variants[1] ?? {};
    const isBaseAlsoVariant =
        variants.findIndex(
            (variant) => variant?.lightness === baseColorLightness,
        ) > -1;
    let isBaseColorOutsideVariantRange = false;
    let shouldReverseLogic = false;
    // Determine the step used for variants
    let variantsStep = variantA.lightness - variantB.lightness;
    // In the event the sort order of the variants changes,
    // check for negative value and fix
    variantsStep = variantsStep < 0 ? variantsStep * -1 : variantsStep;

    let potentialHoverLightnesses = isDark
        ? variantsLightnessArr.filter(
              (variantLightness) => variantLightness < baseColorLightness,
          )
        : variantsLightnessArr.filter(
              (variantLightness) => variantLightness > baseColorLightness,
          );

    // If there are no options for potential hover lightness that means the baseColorLightness
    // is lighter/darker than the lights/darkest variants available and we should
    // pick a hover color from all the variants.
    if (potentialHoverLightnesses.length === 0) {
        isBaseColorOutsideVariantRange = true;
        shouldReverseLogic = true;
        potentialHoverLightnesses = [...variantsLightnessArr];
    }

    // If there are less than 2 options for potential hover lightness, that means
    // we don't have enough options to choose from for a hover color and
    // we should reverse our original filtering for light/dark check.
    if (potentialHoverLightnesses.length < 2) {
        shouldReverseLogic = true;
        potentialHoverLightnesses = isDark
            ? variantsLightnessArr.filter(
                  (variantLightness) => variantLightness > baseColorLightness,
              )
            : variantsLightnessArr.filter(
                  (variantLightness) => variantLightness < baseColorLightness,
              );
    }

    // If the baseColorLightness is also a variant
    // remove it from potential hover lightnesses.
    if (isBaseAlsoVariant) {
        potentialHoverLightnesses = potentialHoverLightnesses.filter(
            (potentialHoverLightness) =>
                potentialHoverLightness !== baseColorLightness,
        );
    }

    // Set closest lightness to lightest value by default
    let closestVariantLightness = Math.max(...potentialHoverLightnesses);

    // Handle scenarios where the closest variant lightness
    // should be the darkest variant
    if (
        // If the base color is outside the range of the variants
        // and is perceived as dark
        (isBaseColorOutsideVariantRange && isDark) ||
        // If the base color is not outside the range of the variants
        // and is perceived as dark
        // and did not have enough potential hover lightnesses on the first check
        (!isBaseColorOutsideVariantRange && isDark && shouldReverseLogic) ||
        // If the base color is not outside the range of the variants
        // and is not perceived as dark
        // and had enough potential hover lightnesses on the first check
        (!isBaseColorOutsideVariantRange && !isDark && !shouldReverseLogic) ||
        // If the base color is not outside the range of the variants
        // and is perceived as dark
        // and did not have enough potential hover lightnesses on the first check
        (!isBaseColorOutsideVariantRange && isDark && shouldReverseLogic)
    ) {
        closestVariantLightness = Math.min(...potentialHoverLightnesses);
    }

    const multiplier = isDark ? -1 : 1;
    const recommendedHoverLightness =
        closestVariantLightness +
        variantsStep * (shouldReverseLogic ? multiplier * -1 : multiplier);

    return variants.find(
        (variant) => variant?.lightness === recommendedHoverLightness,
    );
};

const getPrimaryBtnColors = (sourceColors = {}) => {
    const { base = {} } = sourceColors;
    const { hex } = base;
    const hoverBGColor = getPrimaryBtnHoverBGColor(sourceColors);
    const DEFAULT_TEXT = getTextColor(hex);
    const DEFAULT_BG = hex;
    const HOVER_BG = hoverBGColor?.hex;
    const HOVER_TEXT = getTextColor(HOVER_BG);
    const FOCUS_HALO_COLOR = hex;
    const FOCUS_HALO = convertColorToRGBA(FOCUS_HALO_COLOR, 0.4, true);

    return {
        DEFAULT_TEXT,
        DEFAULT_BG,
        HOVER_TEXT,
        HOVER_BG,
        FOCUS_HALO,
    };
};

const getSecondaryDefaultTextColor = (
    sourceColors = {},
    canvasColor = {},
    shouldAutoCorrectColors = true,
) => {
    const { base = {}, variants = [] } = sourceColors;
    const { hex: defaultBtnBGHex } = canvasColor;
    const shouldUseBaseForDefaultColor = !shouldAutoCorrectColors
        ? true
        : getIsReadable(base.hex, defaultBtnBGHex);

    if (shouldUseBaseForDefaultColor) {
        return base.hex;
    }

    const bgColorData =
        getColorCorrelationsData(canvasColor, [base, ...variants]) || {};
    const recommendedColor =
        getRecommendedColor(bgColorData, [base, ...variants], true) || {};

    return recommendedColor.hex;
};

const getSecondaryBtnColors = (
    sourceColors = {},
    canvasColor = {},
    config = {},
) => {
    const { base = {}, variants = [] } = sourceColors;
    const {
        isDarkMode = false,
        shouldAutoCorrectColors = true,
        isMobile,
    } = config;
    const defaultHex = getSecondaryDefaultTextColor(
        sourceColors,
        canvasColor,
        shouldAutoCorrectColors,
    );
    const hoverLightness = isDarkMode ? 10 : 90;
    const FOCUS_HALO_COLOR = defaultHex;
    const {
        hex: hoverBGHex,
        compatibleTextColors: hoverCompatibleTextColors = [],
    } =
        variants.find((variant) => variant.id === hoverLightness.toString()) ||
        {};

    const hasCompatColors = hoverCompatibleTextColors.length > 0;
    const canUseBaseForHover =
        defaultHex !== base.hex &&
        hoverCompatibleTextColors.findIndex((colorId) => colorId === 'base') >
            -1;
    const hoverTextColor = canUseBaseForHover
        ? base.hex
        : variants.find((variant) => {
              const lightestId = hasCompatColors
                  ? Math.max(
                        ...hoverCompatibleTextColors.filter(
                            (colorId) => colorId !== 'base',
                        ),
                    ).toString()
                  : undefined;

              return variant.id === lightestId;
          })?.hex;

    const DEFAULT_TEXT = defaultHex;
    const DEFAULT_BG = canvasColor.hex;
    const DEFAULT_BORDER = isMobile ? defaultHex : 'currentColor';
    const HOVER_TEXT = hoverTextColor;
    const HOVER_BG = hoverBGHex;
    const HOVER_BORDER = isMobile ? hoverTextColor : 'currentColor';
    const FOCUS_HALO = convertColorToRGBA(FOCUS_HALO_COLOR, 0.4, true);

    return {
        DEFAULT_TEXT,
        DEFAULT_BG,
        DEFAULT_BORDER,
        HOVER_TEXT,
        HOVER_BG,
        HOVER_BORDER,
        FOCUS_HALO,
    };
};

export const getBtnStyles = (data = {}) => {
    const apiKeyBorderRadius = getThemeAPIKeyFromName(BTN_BORDER_RADIUS);
    const apiKeyBorderWidth = getThemeAPIKeyFromName(BTN_BORDER_WIDTH);
    const apiKeyFontWeight = getThemeAPIKeyFromName(BTN_FONT_WEIGHT);
    const defaultBorderRadius = BTN_STYLE_TOKEN_DEFAULTS[BTN_BORDER_RADIUS];
    const defaultBorderWidth = BTN_STYLE_TOKEN_DEFAULTS[BTN_BORDER_WIDTH];
    const defaultFontWeight = BTN_STYLE_TOKEN_DEFAULTS[BTN_FONT_WEIGHT];
    const {
        [apiKeyBorderRadius]: borderRadius = defaultBorderRadius,
        [apiKeyBorderWidth]: borderWidth = defaultBorderWidth,
        [apiKeyFontWeight]: fontWeight = defaultFontWeight,
    } = data;

    return {
        [apiKeyBorderRadius]: borderRadius,
        [apiKeyBorderWidth]: borderWidth,
        [apiKeyFontWeight]: fontWeight,
    };
};

export const getBtnSecondaryTheme = (colors = {}, config = {}) => {
    const { sourceColors = {}, canvasColor = {} } = colors;
    const {
        isDarkMode,
        shouldAutoCorrectColors = true,
        isMobile = false,
        isDestructive = false,
    } = config;
    const {
        DEFAULT_TEXT,
        DEFAULT_BG,
        DEFAULT_BORDER,
        HOVER_TEXT,
        HOVER_BG,
        HOVER_BORDER,
        FOCUS_HALO,
    } = getSecondaryBtnColors(sourceColors, canvasColor, {
        isDarkMode,
        isMobile,
        shouldAutoCorrectColors,
    });
    const BTN_DEFAULT_TEXT = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_COLOR_TEXT
        : BTN_SECONDARY_COLOR_TEXT;
    const BTN_DEFAULT_BG = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_COLOR_BG
        : BTN_SECONDARY_COLOR_BG;
    const BTN_DEFAULT_BORDER = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_COLOR_BORDER
        : BTN_SECONDARY_COLOR_BORDER;
    const BTN_ACTIVE_TEXT = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_ACTIVE_COLOR_TEXT
        : BTN_SECONDARY_ACTIVE_COLOR_TEXT;
    const BTN_ACTIVE_BG = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_ACTIVE_COLOR_BG
        : BTN_SECONDARY_ACTIVE_COLOR_BG;
    const BTN_ACTIVE_BORDER = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_ACTIVE_COLOR_BORDER
        : BTN_SECONDARY_ACTIVE_COLOR_BORDER;
    const BTN_HOVER_TEXT = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_HOVER_COLOR_TEXT
        : BTN_SECONDARY_HOVER_COLOR_TEXT;
    const BTN_HOVER_BG = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_HOVER_COLOR_BG
        : BTN_SECONDARY_HOVER_COLOR_BG;
    const BTN_HOVER_BORDER = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_HOVER_COLOR_BORDER
        : BTN_SECONDARY_HOVER_COLOR_BORDER;
    const BTN_FOCUS_HALO = isDestructive
        ? BTN_SECONDARY_DESTRUCTIVE_FOCUS_COLOR_HALO
        : BTN_SECONDARY_FOCUS_COLOR_HALO;
    const BTN_SPLIT_BORDER = isDestructive
        ? BTN_SPLIT_SECONDARY_DESTRUCTIVE_COLOR_BORDER
        : BTN_SPLIT_SECONDARY_COLOR_BORDER;

    return {
        [getThemeAPIKeyFromName(BTN_DEFAULT_TEXT)]: DEFAULT_TEXT,
        [getThemeAPIKeyFromName(BTN_DEFAULT_BG)]: DEFAULT_BG,
        [getThemeAPIKeyFromName(BTN_DEFAULT_BORDER)]: DEFAULT_BORDER,
        ...(!isMobile && {
            [getThemeAPIKeyFromName(BTN_HOVER_TEXT)]: HOVER_TEXT,
            [getThemeAPIKeyFromName(BTN_HOVER_BG)]: HOVER_BG,
            [getThemeAPIKeyFromName(BTN_HOVER_BORDER)]: HOVER_BORDER,
            [getThemeAPIKeyFromName(BTN_FOCUS_HALO)]: FOCUS_HALO,
        }),
        ...(isMobile && {
            [getThemeAPIKeyFromName(BTN_ACTIVE_TEXT)]: HOVER_TEXT,
            [getThemeAPIKeyFromName(BTN_ACTIVE_BG)]: HOVER_BG,
            [getThemeAPIKeyFromName(BTN_ACTIVE_BORDER)]: HOVER_BORDER,
        }),
        [getThemeAPIKeyFromName(BTN_SPLIT_BORDER)]: HOVER_TEXT,
    };
};

export const getBtnPrimaryTheme = (colors = {}, config = {}) => {
    const { sourceColors = {} } = colors;
    const {
        shouldAutoCorrectColors = true,
        isMobile = false,
        isDestructive = false,
    } = config;
    const {
        base = {},
        variants = [],
        recommendedNonTextColor = {},
    } = sourceColors;
    const evaluatedColorData = {
        base: shouldAutoCorrectColors ? recommendedNonTextColor : base,
        variants,
    };

    const { DEFAULT_TEXT, DEFAULT_BG, HOVER_TEXT, HOVER_BG, FOCUS_HALO } =
        getPrimaryBtnColors(evaluatedColorData);
    const BTN_DEFAULT_TEXT = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_COLOR_TEXT
        : BTN_PRIMARY_COLOR_TEXT;
    const BTN_DEFAULT_BG = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_COLOR_BG
        : BTN_PRIMARY_COLOR_BG;
    const BTN_DEFAULT_BORDER = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_COLOR_BORDER
        : BTN_PRIMARY_COLOR_BORDER;
    const BTN_ACTIVE_TEXT = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_ACTIVE_COLOR_TEXT
        : BTN_PRIMARY_ACTIVE_COLOR_TEXT;
    const BTN_ACTIVE_BG = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_ACTIVE_COLOR_BG
        : BTN_PRIMARY_ACTIVE_COLOR_BG;
    const BTN_ACTIVE_BORDER = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_ACTIVE_COLOR_BORDER
        : BTN_PRIMARY_ACTIVE_COLOR_BORDER;
    const BTN_HOVER_TEXT = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_HOVER_COLOR_TEXT
        : BTN_PRIMARY_HOVER_COLOR_TEXT;
    const BTN_HOVER_BG = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_HOVER_COLOR_BG
        : BTN_PRIMARY_HOVER_COLOR_BG;
    const BTN_HOVER_BORDER = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_HOVER_COLOR_BORDER
        : BTN_PRIMARY_HOVER_COLOR_BORDER;
    const BTN_FOCUS_HALO = isDestructive
        ? BTN_PRIMARY_DESTRUCTIVE_FOCUS_COLOR_HALO
        : BTN_PRIMARY_FOCUS_COLOR_HALO;
    const BTN_SPLIT_BORDER = isDestructive
        ? BTN_SPLIT_PRIMARY_DESTRUCTIVE_COLOR_BORDER
        : BTN_SPLIT_PRIMARY_COLOR_BORDER;

    return {
        [getThemeAPIKeyFromName(BTN_DEFAULT_TEXT)]: DEFAULT_TEXT,
        [getThemeAPIKeyFromName(BTN_DEFAULT_BG)]: DEFAULT_BG,
        [getThemeAPIKeyFromName(BTN_DEFAULT_BORDER)]: isMobile
            ? `${DEFAULT_BG}00`
            : 'transparent',
        ...(!isMobile && {
            [getThemeAPIKeyFromName(BTN_HOVER_TEXT)]: HOVER_TEXT,
            [getThemeAPIKeyFromName(BTN_HOVER_BG)]: HOVER_BG,
            [getThemeAPIKeyFromName(BTN_HOVER_BORDER)]: 'transparent',
            [getThemeAPIKeyFromName(BTN_FOCUS_HALO)]: FOCUS_HALO,
        }),
        ...(isMobile && {
            [getThemeAPIKeyFromName(BTN_ACTIVE_TEXT)]: HOVER_TEXT,
            [getThemeAPIKeyFromName(BTN_ACTIVE_BG)]: HOVER_BG,
            [getThemeAPIKeyFromName(BTN_ACTIVE_BORDER)]: isMobile
                ? `${HOVER_BG}00`
                : 'transparent',
        }),
        [getThemeAPIKeyFromName(BTN_SPLIT_BORDER)]: HOVER_BG,
    };
};

export const getDialogActionStyle = (data = {}, config = {}) => {
    const { isMobile } = config;
    const apiKeyBorderRadius = getThemeAPIKeyFromName(
        DIALOG_ACTION_BORDER_RADIUS,
    );
    const defaultBorderRadius = isMobile
        ? CORE_SYSTEM_SCALE * 8
        : DIALOG_ACTION_STYLE_TOKEN_DEFAULTS[DIALOG_ACTION_BORDER_RADIUS];

    const { [apiKeyBorderRadius]: borderRadius = defaultBorderRadius } = data;
    const themeData = {
        [apiKeyBorderRadius]: borderRadius,
    };

    return themeData;
};

export const getDialogActionTheme = (colors = {}, config = {}) => {
    const { sourceColors = {} } = colors;
    const { isDarkMode, isMobile, shouldAutoCorrectColors } = config;
    const { base = {}, variants = [] } = sourceColors;
    const hoverLightness = isDarkMode ? 10 : 90;
    const apiKeyBGColor = getThemeAPIKeyFromName(DIALOG_ACTION_COLOR_BG);
    const apiKeyIconColor = getThemeAPIKeyFromName(DIALOG_ACTION_COLOR_ICON);
    const apiKeyInteractionStateBGColor = getThemeAPIKeyFromName(
        isMobile ? DIALOG_ACTION_ACTIVE_COLOR_BG : DIALOG_ACTION_HOVER_COLOR_BG,
    );
    const apiKeyInteractionStateIconColor = getThemeAPIKeyFromName(
        isMobile
            ? DIALOG_ACTION_ACTIVE_COLOR_ICON
            : DIALOG_ACTION_HOVER_COLOR_ICON,
    );
    const defaultBGColor = isDarkMode
        ? variants.find((variant) => variant.lightness === 5)
        : variants.find((variant) => variant.lightness === 95);
    const defaultInteractionStateBGColor = variants.find(
        (variant) => variant.lightness === hoverLightness,
    );
    const defaultIconColor = shouldAutoCorrectColors
        ? getRecommendedColor(defaultBGColor, [
              base,
              ...variants.filter(
                  (variant) => variant.lightness !== defaultBGColor.lightness,
              ),
          ])
        : base;
    const defaultInteractionStateIconColor = getRecommendedColor(
        {
            ...defaultInteractionStateBGColor,
            compatibleNonTextColors:
                // Remove base so it is not used for both resting and interactive state
                (
                    defaultInteractionStateBGColor?.compatibleNonTextColors ??
                    []
                ).filter(
                    (compatibleNonTextColor) =>
                        compatibleNonTextColor !== 'base',
                ),
        },
        variants.filter(
            (variant) =>
                variant.lightness !== defaultInteractionStateBGColor.lightness,
        ),
    );
    const themeData = {
        [apiKeyBGColor]: defaultBGColor.hex,
        [apiKeyIconColor]: defaultIconColor.hex,
        [apiKeyInteractionStateBGColor]: defaultInteractionStateBGColor.hex,
        [apiKeyInteractionStateIconColor]: defaultInteractionStateIconColor.hex,
    };

    return themeData;
};
