import { PureComponent, Fragment } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { LoadingDots } from '@Green-Dot-Corporation/eureka-ui-loading';
import { v4 as uuidv4 } from 'uuid';
import ButtonIcon from './components/ButtonIcon';
import ButtonText from './components/ButtonText';

// Utils
import { getBtnModCls } from './utils/buttonUtils';

// Styles
import './styles/file-picker-button.scss';

class FilePickerButton extends PureComponent {
    static defaultProps = {
        isBusy: false,
        isDisabled: false,
        shouldAllowMultiple: false,
        onChange: () => Promise.resolve(),
    };

    static propTypes = {
        'aria-label': PropTypes.string,
        contextCls: PropTypes.string,
        id: PropTypes.string,
        fileTypes: PropTypes.string,
        text: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
        variant: PropTypes.string,
        value: PropTypes.string,
        isBusy: PropTypes.bool,
        isDisabled: PropTypes.bool,
        shouldAllowMultiple: PropTypes.bool,
        onChange: PropTypes.func,
        renderIcon: PropTypes.func,
        renderBusyIcon: PropTypes.func,
    };

    render() {
        const { contextCls } = this.props;
        const modCls = this.getModCls();
        const props = this.getExtantProps();

        return (
            <span className={cx(this.baseCls, modCls, contextCls)}>
                <input
                    {...props}
                    className={`${this.baseCls}__input`}
                    type="file"
                    onChange={this.handleChange}
                />
                {this.renderFauxButton()}
            </span>
        );
    }

    renderFauxButton = () => {
        const { children, id } = this.getExtantProps();

        return (
            <label className={`${this.baseCls}__button`} htmlFor={id}>
                <span className={`${this.baseCls}__content-container`}>
                    {!children && this.renderMain()}
                    {children}
                </span>
            </label>
        );
    };

    renderMain = () => {
        const { text, 'aria-label': ariaLabel, isBusy } = this.props;
        const shouldRenderBtnIcon = this.getShouldRenderBtnIcon();
        const shouldRenderText = !!text || !!ariaLabel;

        return (
            <Fragment>
                {shouldRenderBtnIcon && this.renderIcon()}
                {isBusy && this.renderIcon(isBusy)}
                {!!shouldRenderText && this.renderText()}
            </Fragment>
        );
    };

    renderIcon = (isBusyIcon) => {
        const { renderBusyIcon, renderIcon } = this.props;
        const busyIconRenderer = renderBusyIcon || this.renderBusyIcon;
        const iconRenderer = isBusyIcon ? busyIconRenderer : renderIcon;

        return <ButtonIcon baseCls={this.baseCls} renderIcon={iconRenderer} />;
    };

    renderBusyIcon = () => {
        return <LoadingDots contextCls={`${this.baseCls}__busy-icon`} />;
    };

    renderText = () => {
        const { text, 'aria-label': ariaLabel } = this.props;

        return (
            <ButtonText baseCls={this.baseCls}>{text ?? ariaLabel}</ButtonText>
        );
    };

    handleChange = (e) => {
        const { onChange } = this.props;
        const { files } = e?.target ?? {};

        onChange({ ...(files && { files }), e });
    };

    getShouldRenderBtnIcon = () => {
        const { isBusy, renderIcon } = this.props;

        return !isBusy && !!renderIcon;
    };

    getModCls = () => {
        const { variant, isBusy, text, 'aria-label': ariaLabel } = this.props;

        return {
            ...getBtnModCls(variant, this.baseCls),
            [`${this.baseCls}--busy`]: isBusy,
            [`${this.baseCls}--should-visibly-hide-text`]: !text && !!ariaLabel,
        };
    };

    getExtantProps = () => {
        const {
            'aria-label': ariaLabel,
            contextCls,
            id,
            text,
            fileTypes,
            variant,
            isBusy,
            isDisabled,
            shouldAllowMultiple,
            onChange,
            renderIcon,
            renderBusyIcon,
            children,
            ...rest
        } = this.props;

        return {
            ...rest,
            id: id ?? this.id,
            accept: fileTypes,
            ...(shouldAllowMultiple && { multiple: true }),
            disabled: isBusy || isDisabled,
        };
    };

    id = `eureka-ff-${uuidv4()}`;

    baseCls = 'eureka-file-picker-button';
}

export default FilePickerButton;
