/* eslint-disable import/prefer-default-export */
import { getThemeAPIKeyFromName } from '../dataUtils';
import { CAROUSEL_TOKEN_NAMES } from '../../const/tokens/carouselTokens';

const { CAROUSEL_ACTIVE_PAGINATION_COLOR, CAROUSEL_INACTIVE_PAGINATION_COLOR } =
    CAROUSEL_TOKEN_NAMES;

export const getCarouselTheme = (colors = {}, config = {}) => {
    const { sourceColors = {}, textAltColor = {} } = colors;
    const { shouldAutoCorrectColors = true } = config;
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;
    const tokens = {
        [getThemeAPIKeyFromName(CAROUSEL_ACTIVE_PAGINATION_COLOR)]:
            shouldAutoCorrectColors ? recommendedNonTextColor.hex : base.hex,
        [getThemeAPIKeyFromName(CAROUSEL_INACTIVE_PAGINATION_COLOR)]:
            textAltColor.hex,
    };

    return tokens;
};
