/* eslint-disable import/prefer-default-export */
import {
    getNewColorByChangingLightness,
    convertColorToRGB,
} from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getThemeAPIKeyFromName } from '../dataUtils';
import { LOADING_COLOR_TOKEN_NAMES } from '../../const/tokens/loadingTokens';

export const getLoadingTheme = (colors = {}, config = {}) => {
    const { sourceColors = {}, canvasColor = {} } = colors;
    const { shouldAutoCorrectColors = true, isDarkMode, isMobile } = config;
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;
    const bg = getNewColorByChangingLightness(
        canvasColor.hex,
        isDarkMode ? 20 : 90,
    );
    const { r, g, b } = convertColorToRGB('#ffffff');

    return {
        [getThemeAPIKeyFromName(LOADING_COLOR_TOKEN_NAMES.LOADING_COLOR_ICON)]:
            shouldAutoCorrectColors ? recommendedNonTextColor.hex : base.hex,
        ...(!isMobile && {
            [getThemeAPIKeyFromName(
                LOADING_COLOR_TOKEN_NAMES.LOADING_SKELETON_COLOR_BG,
            )]: bg,
            [getThemeAPIKeyFromName(
                LOADING_COLOR_TOKEN_NAMES.LOADING_SKELETON_COLOR_ANIMATION,
            )]: `${r}, ${g}, ${b}`,
        }),
    };
};
