import PropTypes from 'prop-types';
import cx from 'classnames';

// Styles
import './styles/resource-list-item-nav-action-icon.scss';

const ResourceListItemNavActionIcon = (props) => {
    const { contextCls, renderIcon } = props;
    const baseCls = 'eureka-resource-list-item-nav-action-icon';

    return (
        <span className={cx(baseCls, contextCls)}>
            <span className={`${baseCls}__icon-safe-space`}>
                {renderIcon && renderIcon(`${baseCls}__icon`)}
            </span>
        </span>
    );
};

ResourceListItemNavActionIcon.propTypes = {
    contextCls: PropTypes.string,
    renderIcon: PropTypes.func,
};

export default ResourceListItemNavActionIcon;
