import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';

// Styles
import './styles/loading-dots.scss';

class LoadingDots extends PureComponent {
    static defaultProps = {};

    static propTypes = {
        contextCls: PropTypes.string,
    };

    render() {
        const { contextCls } = this.props;

        return (
            <span className={cx(this.baseCls, contextCls)}>
                {this.renderDot()}
                {this.renderDot()}
                {this.renderDot()}
            </span>
        );
    }

    renderDot = () => {
        return <span className={`${this.baseCls}__dot`} />;
    };

    baseCls = 'eureka-loading-dots';
}

export default LoadingDots;
