import { Component } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { EurekaChevronRight } from '@Green-Dot-Corporation/eureka-ui-icons';
import ListItem from './ListItem';
import ResourceListItemIcon from './ResourceListItemIcon';
import ResourceListItemNavActionIcon from './ResourceListItemNavActionIcon';

// Styles
import './styles/resource-list-item.scss';

class ResourceListItem extends Component {
    static defaultProps = {
        isDisabled: false,
        isInteractive: false,
        shouldIconUseColor: true,
        shouldRenderAfterIcon: false,
        shouldSwapTextAndMeta: false,
        onClick: () => Promise.resolve(),
    };

    static propTypes = {
        contextCls: PropTypes.string,
        meta: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
        text: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
        isDisabled: PropTypes.bool,
        isInteractive: PropTypes.bool,
        shouldIconUseColor: PropTypes.bool,
        shouldRenderAfterIcon: PropTypes.bool,
        shouldSwapTextAndMeta: PropTypes.bool,
        data: PropTypes.object,
        onClick: PropTypes.func,
        renderAfter: PropTypes.func,
        renderBefore: PropTypes.func,
        renderIcon: PropTypes.func,
    };

    render() {
        const { contextCls } = this.props;
        const props = this.getExtantProps();
        const listItemProps = this.getListItemProps();
        const modCls = this.getModCls();
        const HTMLEl = this.getHTMLEl();

        return (
            <HTMLEl {...props} className={cx(this.baseCls, modCls, contextCls)}>
                <ListItem
                    {...listItemProps}
                    contextCls={`${this.baseCls}__list-item`}
                />
            </HTMLEl>
        );
    }

    renderIcon = () => {
        const { renderIcon, shouldIconUseColor } = this.props;

        return (
            <ResourceListItemIcon
                contextCls={`${this.baseCls}__icon-container`}
                renderIcon={renderIcon}
                shouldUseColor={shouldIconUseColor}
            />
        );
    };

    renderAfter = () => {
        const { renderAfter } = this.props;

        if (renderAfter) {
            return renderAfter();
        }

        return (
            <ResourceListItemNavActionIcon
                contextCls={`${this.baseCls}__after-icon-container`}
                renderIcon={this.renderAfterIcon}
            />
        );
    };

    renderAfterIcon = (iconCls) => {
        return (
            <EurekaChevronRight
                contextCls={cx(`${this.baseCls}__after-icon`, iconCls)}
            />
        );
    };

    handleClick = (e) => {
        const { onClick, data } = this.props;

        onClick({ e, ...(!!data && { data }) });
    };

    getShouldRenderAfter = () => {
        const { shouldRenderAfterIcon, renderAfter } = this.props;

        return !!renderAfter || shouldRenderAfterIcon;
    };

    getHTMLEl = () => {
        const { isInteractive } = this.props;

        return isInteractive ? 'button' : 'span';
    };

    getModCls = () => {
        const { isInteractive } = this.props;

        return {
            [`${this.baseCls}--interactive`]: isInteractive,
        };
    };

    getListItemProps = () => {
        const { meta, text, shouldSwapTextAndMeta, renderIcon, renderBefore } =
            this.props;
        const shouldRenderAfterIcon = this.getShouldRenderAfter();

        return {
            meta,
            text,
            shouldSwapTextAndMeta,
            ...(shouldRenderAfterIcon && { renderAfter: this.renderAfter }),
            ...((renderIcon || renderBefore) && {
                renderBefore: renderBefore ?? this.renderIcon,
            }),
        };
    };

    getExtantProps = () => {
        const {
            contextCls,
            meta,
            text,
            isDisabled,
            isInteractive,
            shouldIconUseColor,
            shouldRenderAfterIcon,
            shouldSwapTextAndMeta,
            data,
            onClick,
            renderIcon,
            renderAfter,
            ...rest
        } = this.props;

        return {
            ...rest,
            ...(isInteractive && isDisabled && { disabled: true }),
            ...(isInteractive && { onClick: this.handleClick }),
        };
    };

    baseCls = 'eureka-resource-list-item';
}

export default ResourceListItem;
