import { Fragment, Component } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';

// Styles
import './styles/list-item.scss';

class ListItem extends Component {
    static defaultProps = {
        shouldSwapTextAndMeta: false,
    };

    static propTypes = {
        contextCls: PropTypes.string,
        meta: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
        text: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
        shouldSwapTextAndMeta: PropTypes.bool,
        renderBefore: PropTypes.func,
        renderAfter: PropTypes.func,
    };

    render() {
        const {
            contextCls,
            shouldSwapTextAndMeta,
            renderBefore,
            renderAfter,
            children,
        } = this.props;

        return (
            <span className={cx(this.baseCls, contextCls)}>
                <span className={`${this.baseCls}__inner`}>
                    {renderBefore && (
                        <span className={`${this.baseCls}__before-container`}>
                            {renderBefore()}
                        </span>
                    )}
                    <span className={`${this.baseCls}__text-container`}>
                        {!children &&
                            !shouldSwapTextAndMeta &&
                            this.renderTextContent()}
                        {!children &&
                            shouldSwapTextAndMeta &&
                            this.renderTextSwappedContent()}
                        {children}
                    </span>
                    {renderAfter && (
                        <span className={`${this.baseCls}__after-container`}>
                            {renderAfter()}
                        </span>
                    )}
                </span>
            </span>
        );
    }

    renderTextContent = () => {
        const { text, meta } = this.props;

        return (
            <Fragment>
                {text && (
                    <span className={`${this.baseCls}__text`}>{text}</span>
                )}
                {meta && (
                    <span className={`${this.baseCls}__meta`}>{meta}</span>
                )}
            </Fragment>
        );
    };

    renderTextSwappedContent = () => {
        const { text, meta } = this.props;

        return (
            <Fragment>
                {meta && (
                    <span className={`${this.baseCls}__meta`}>{meta}</span>
                )}
                {text && (
                    <span className={`${this.baseCls}__text`}>{text}</span>
                )}
            </Fragment>
        );
    };

    baseCls = 'eureka-list-item';
}

export default ListItem;
