import { getThemeAPIKeyFromName } from '../dataUtils';
import { CHAT_TOKEN_NAMES } from '../../const/tokens/chatTokens';
import { CHAT_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/chatTokenDefaults';
import { CORE_STYLE_TOKEN_NAMES } from '../../const/tokens/coreTokens';
import { CORE_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/coreTokenDefaults';

const {
    CHAT_ACTION_SECONDARY_COLOR_BORDER,
    CHAT_ACTION_SECONDARY_ACTIVE_COLOR_BORDER,
    CHAT_ACTION_SECONDARY_HOVER_COLOR_BORDER,
    CHAT_BUBBLE_BORDER_RADIUS,
    CHAT_FLOATING_BTN_ACTIVE_COLOR_BORDER,
    CHAT_FLOATING_BTN_COLOR_BORDER,
    CHAT_FLOATING_BTN_HOVER_COLOR_BORDER,
    CHAT_ACTION_BORDER_RADIUS,
    CHAT_FLOATING_BTN_BORDER_RADIUS,
    CHAT_WINDOW_BORDER_RADIUS,
} = CHAT_TOKEN_NAMES;
const { CORE_SYSTEM_SCALE } = CORE_STYLE_TOKEN_NAMES;

export const getChatStyle = (data = {}, config = {}) => {
    const { isMobile } = config;
    const apiKeyActionBorderRadius = getThemeAPIKeyFromName(
        CHAT_ACTION_BORDER_RADIUS,
    );
    const apiKeyFloatingBtnBorderRadius = getThemeAPIKeyFromName(
        CHAT_FLOATING_BTN_BORDER_RADIUS,
    );
    const apiKeyChatBubbleBorderRadius = getThemeAPIKeyFromName(
        CHAT_BUBBLE_BORDER_RADIUS,
    );
    const apiKeyWindowBorderRadius = getThemeAPIKeyFromName(
        CHAT_WINDOW_BORDER_RADIUS,
    );
    const defaultActionBorderRadius = isMobile
        ? CORE_STYLE_TOKEN_DEFAULTS[CORE_SYSTEM_SCALE] * 8
        : CHAT_STYLE_TOKEN_DEFAULTS[CHAT_ACTION_BORDER_RADIUS];
    const defaulFloatingBtnBorderRadius = isMobile
        ? CORE_STYLE_TOKEN_DEFAULTS[CORE_SYSTEM_SCALE] * 16
        : CHAT_STYLE_TOKEN_DEFAULTS[CHAT_FLOATING_BTN_BORDER_RADIUS];
    const defaultChatBubbleBorderRadius =
        CHAT_STYLE_TOKEN_DEFAULTS[CHAT_BUBBLE_BORDER_RADIUS];
    const defaultWindowborderRadius =
        CHAT_STYLE_TOKEN_DEFAULTS[CHAT_WINDOW_BORDER_RADIUS];
    const {
        [apiKeyActionBorderRadius]:
            actionBorderRadius = defaultActionBorderRadius,
        [apiKeyFloatingBtnBorderRadius]:
            floatingBtnBorderRadius = defaulFloatingBtnBorderRadius,
        [apiKeyChatBubbleBorderRadius]:
            chatBubbleBorderRadius = defaultChatBubbleBorderRadius,
        [apiKeyWindowBorderRadius]:
            chatWindowBorderRadius = defaultWindowborderRadius,
    } = data;
    const themeData = {
        [apiKeyActionBorderRadius]: actionBorderRadius,
        [apiKeyFloatingBtnBorderRadius]: floatingBtnBorderRadius,
        [apiKeyChatBubbleBorderRadius]: chatBubbleBorderRadius,
        [apiKeyWindowBorderRadius]: chatWindowBorderRadius,
    };

    return themeData;
};

export const getChatTheme = (colors = {}, config = {}) => {
    const { sourceColors = {} } = colors;
    const { isMobile, shouldAutoCorrectColors } = config;
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;
    const apiKeyActionSecondaryBorderColor = getThemeAPIKeyFromName(
        CHAT_ACTION_SECONDARY_COLOR_BORDER,
    );
    const apiKeySecondaryActionInteractiveStateBorderColor =
        getThemeAPIKeyFromName(
            isMobile
                ? CHAT_ACTION_SECONDARY_ACTIVE_COLOR_BORDER
                : CHAT_ACTION_SECONDARY_HOVER_COLOR_BORDER,
        );
    const apiKeyFloatingBtnBorderColor = getThemeAPIKeyFromName(
        CHAT_FLOATING_BTN_COLOR_BORDER,
    );
    const apiKeyFloatingBtnInteractiveStateBorderColor = getThemeAPIKeyFromName(
        isMobile
            ? CHAT_FLOATING_BTN_ACTIVE_COLOR_BORDER
            : CHAT_FLOATING_BTN_HOVER_COLOR_BORDER,
    );
    const sourceColor = shouldAutoCorrectColors
        ? recommendedNonTextColor
        : base;
    const themeData = {
        [apiKeyActionSecondaryBorderColor]: isMobile
            ? `${sourceColor.hex}00`
            : 'transparent',
        [apiKeySecondaryActionInteractiveStateBorderColor]: isMobile
            ? `${sourceColor.hex}00`
            : 'transparent',
        [apiKeyFloatingBtnBorderColor]: isMobile
            ? `${sourceColor.hex}00`
            : 'transparent',
        [apiKeyFloatingBtnInteractiveStateBorderColor]: isMobile
            ? `${sourceColor.hex}00`
            : 'transparent',
    };

    return themeData;
};
