import { getThemeAPIKeyFromName } from '../../dataUtils';
import { CORE_TYPOGRAPHY_STYLE_TOKEN_NAMES } from '../../../const/tokens/coreTokens';
import { MOBILE_APP_HEADER_TOKEN_NAMES } from '../../../const/tokens/mobileAppHeaderTokens';
import { MOBILE_APP_TABSTRIP_TOKEN_NAMES } from '../../../const/tokens/mobileTabstripTokens';
import { MOBILE_SPACING_TOKEN_NAMES } from '../../../const/tokens/mobileSpacingTokens';
import { MOBILE_SPACING_STYLE_TOKEN_DEFAULTS } from '../../../const/token-defaults/mobileSpacingTokenDefaults';

const { CORE_TYPOGRAPHY_LARGE_FONT_SIZE } = CORE_TYPOGRAPHY_STYLE_TOKEN_NAMES;
const {
    APP_HEADER_TITLE_FONT_SIZE,
    APP_HEADER_ACTIONS_COLOR,
    APP_HEADER_COLOR_BG,
    APP_HEADER_COLOR_TEXT,
} = MOBILE_APP_HEADER_TOKEN_NAMES;
const {
    APP_TABSTRIP_ACTIVE_COLOR_TEXT,
    APP_TABSTRIP_COLOR_BG,
    APP_TABSTRIP_INACTIVE_COLOR_TEXT,
    APP_TABSTRIP_TAB_FONT_SIZE,
} = MOBILE_APP_TABSTRIP_TOKEN_NAMES;

const {
    CORE_LAYOUT_SPACING_2X_SMALL,
    CORE_LAYOUT_SPACING_X_SMALL,
    CORE_LAYOUT_SPACING_SMALL,
    CORE_LAYOUT_SPACING_MEDIUM,
    CORE_LAYOUT_SPACING_LARGE,
    CORE_LAYOUT_SPACING_X_LARGE,
    CORE_LAYOUT_SPACING_2X_LARGE,
} = MOBILE_SPACING_TOKEN_NAMES;

export const getMobileAppHeaderTheme = (params = {}, config = {}) => {
    const {
        sourceColors = {},
        canvasColor = {},
        headingsTextColor = {},
        typographyTokens = {},
    } = params;
    const { shouldAutoCorrectColors = true } = config;
    const { base = {}, recommendedTextColor = {} } = sourceColors;
    const titleFontSize =
        typographyTokens[
            getThemeAPIKeyFromName(CORE_TYPOGRAPHY_LARGE_FONT_SIZE)
        ];
    const tokens = {
        [getThemeAPIKeyFromName(APP_HEADER_ACTIONS_COLOR)]:
            shouldAutoCorrectColors ? recommendedTextColor.hex : base.hex,
        [getThemeAPIKeyFromName(APP_HEADER_COLOR_BG)]: canvasColor.hex,
        [getThemeAPIKeyFromName(APP_HEADER_TITLE_FONT_SIZE)]: titleFontSize,
        [getThemeAPIKeyFromName(APP_HEADER_COLOR_TEXT)]: headingsTextColor.hex,
    };

    return tokens;
};

export const getMobileTabStripTheme = (params = {}, config = {}) => {
    const { sourceColors = {}, canvasColor = {}, textAltColor = {} } = params;
    const { shouldAutoCorrectColors = true } = config;
    const { base = {}, recommendedTextColor = {} } = sourceColors;
    const tokens = {
        [getThemeAPIKeyFromName(APP_TABSTRIP_ACTIVE_COLOR_TEXT)]:
            shouldAutoCorrectColors ? recommendedTextColor.hex : base.hex,
        [getThemeAPIKeyFromName(APP_TABSTRIP_COLOR_BG)]: canvasColor.hex,
        [getThemeAPIKeyFromName(APP_TABSTRIP_INACTIVE_COLOR_TEXT)]:
            textAltColor.hex,
        [getThemeAPIKeyFromName(APP_TABSTRIP_TAB_FONT_SIZE)]: 10,
    };

    return tokens;
};

export const getMobileSpacingStyle = (data = {}) => {
    const apiKeySpacing2XSmall = getThemeAPIKeyFromName(
        CORE_LAYOUT_SPACING_2X_SMALL,
    );
    const apiKeySpacingXSmall = getThemeAPIKeyFromName(
        CORE_LAYOUT_SPACING_X_SMALL,
    );
    const apiKeySpacingSmall = getThemeAPIKeyFromName(
        CORE_LAYOUT_SPACING_SMALL,
    );
    const apiKeySpacingMedium = getThemeAPIKeyFromName(
        CORE_LAYOUT_SPACING_MEDIUM,
    );
    const apiKeySpacingLarge = getThemeAPIKeyFromName(
        CORE_LAYOUT_SPACING_LARGE,
    );
    const apiKeySpacingXLarge = getThemeAPIKeyFromName(
        CORE_LAYOUT_SPACING_X_LARGE,
    );
    const apiKeySpacing2XLarge = getThemeAPIKeyFromName(
        CORE_LAYOUT_SPACING_2X_LARGE,
    );

    const defaultSpacing2XSmall =
        MOBILE_SPACING_STYLE_TOKEN_DEFAULTS[CORE_LAYOUT_SPACING_2X_SMALL];
    const defaultSpacingXSmall =
        MOBILE_SPACING_STYLE_TOKEN_DEFAULTS[CORE_LAYOUT_SPACING_X_SMALL];
    const defaultSpacingSmall =
        MOBILE_SPACING_STYLE_TOKEN_DEFAULTS[CORE_LAYOUT_SPACING_SMALL];
    const defaultSpacingMedium =
        MOBILE_SPACING_STYLE_TOKEN_DEFAULTS[CORE_LAYOUT_SPACING_MEDIUM];
    const defaultSpacingLarge =
        MOBILE_SPACING_STYLE_TOKEN_DEFAULTS[CORE_LAYOUT_SPACING_LARGE];
    const defaultSpacingXLarge =
        MOBILE_SPACING_STYLE_TOKEN_DEFAULTS[CORE_LAYOUT_SPACING_X_LARGE];
    const defaultSpacing2XLarge =
        MOBILE_SPACING_STYLE_TOKEN_DEFAULTS[CORE_LAYOUT_SPACING_2X_LARGE];

    const {
        [apiKeySpacing2XSmall]: spacing2Xsmall = defaultSpacing2XSmall,
        [apiKeySpacingXSmall]: spacingXSmall = defaultSpacingXSmall,
        [apiKeySpacingSmall]: spacingSmall = defaultSpacingSmall,
        [apiKeySpacingMedium]: spacingMedium = defaultSpacingMedium,
        [apiKeySpacingLarge]: spacingLarge = defaultSpacingLarge,
        [apiKeySpacingXLarge]: spacingXLarge = defaultSpacingXLarge,
        [apiKeySpacing2XLarge]: spacing2XLarge = defaultSpacing2XLarge,
    } = data;

    return {
        [apiKeySpacing2XSmall]: spacing2Xsmall,
        [apiKeySpacingXSmall]: spacingXSmall,
        [apiKeySpacingSmall]: spacingSmall,
        [apiKeySpacingMedium]: spacingMedium,
        [apiKeySpacingLarge]: spacingLarge,
        [apiKeySpacingXLarge]: spacingXLarge,
        [apiKeySpacing2XLarge]: spacing2XLarge,
    };
};
