import { getNewColorByChangingLightness } from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getTextColor } from './themeUtils';
import { getThemeAPIKeyFromName } from '../dataUtils';
import { CALLOUT_TOKEN_NAMES } from '../../const/tokens/calloutTokens';
import { CALLOUT_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/calloutTokenDefaults';

const {
    CALLOUT_AFFIRMATIVE_COLOR_ICON,
    CALLOUT_CAUTIONARY_COLOR_ICON,
    CALLOUT_COLOR_ICON,
    CALLOUT_ERROR_COLOR_ICON,
    CALLOUT_INFO_COLOR_ICON,
    CALLOUT_MEMO_COLOR_BG,
    CALLOUT_MEMO_COLOR_ICON,
    CALLOUT_MEMO_COLOR_TEXT,
    CALLOUT_MEMO_AFFIRMATIVE_COLOR_BG,
    CALLOUT_MEMO_AFFIRMATIVE_COLOR_ICON,
    CALLOUT_MEMO_AFFIRMATIVE_COLOR_TEXT,
    CALLOUT_MEMO_CAUTIONARY_COLOR_BG,
    CALLOUT_MEMO_CAUTIONARY_COLOR_ICON,
    CALLOUT_MEMO_CAUTIONARY_COLOR_TEXT,
    CALLOUT_MEMO_ERROR_COLOR_BG,
    CALLOUT_MEMO_ERROR_COLOR_ICON,
    CALLOUT_MEMO_ERROR_COLOR_TEXT,
    CALLOUT_MEMO_INFO_COLOR_BG,
    CALLOUT_MEMO_INFO_COLOR_ICON,
    CALLOUT_MEMO_INFO_COLOR_TEXT,
    CALLOUT_MEMO_BORDER_RADIUS,
} = CALLOUT_TOKEN_NAMES;

const getCalloutMemoColors = (sourceColors = {}, config = {}) => {
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;
    const { shouldAutoCorrectColors = true } = config;
    const { hex: BG } = shouldAutoCorrectColors
        ? recommendedNonTextColor
        : base;
    const TEXT = getTextColor(BG);
    const ICON = TEXT;

    return {
        ICON,
        TEXT,
        BG,
    };
};

export const getCalloutMemoStyle = (data = {}) => {
    const apiKeyCMBorderRadius = getThemeAPIKeyFromName(
        CALLOUT_MEMO_BORDER_RADIUS,
    );
    const defaultCMBorderRadius =
        CALLOUT_STYLE_TOKEN_DEFAULTS[CALLOUT_MEMO_BORDER_RADIUS];
    const { [apiKeyCMBorderRadius]: cmBorderRadius = defaultCMBorderRadius } =
        data;

    return {
        [apiKeyCMBorderRadius]: cmBorderRadius,
    };
};

export const getCalloutMemoAffirmativeTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;
    const { ICON, TEXT, BG } = getCalloutMemoColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(CALLOUT_MEMO_AFFIRMATIVE_COLOR_ICON)]: ICON,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_AFFIRMATIVE_COLOR_TEXT)]: TEXT,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_AFFIRMATIVE_COLOR_BG)]: BG,
    };
};

export const getCalloutMemoCautionaryTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;
    const { ICON, TEXT, BG } = getCalloutMemoColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(CALLOUT_MEMO_CAUTIONARY_COLOR_ICON)]: ICON,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_CAUTIONARY_COLOR_TEXT)]: TEXT,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_CAUTIONARY_COLOR_BG)]: BG,
    };
};

export const getCalloutMemoErrorTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;
    const { ICON, TEXT, BG } = getCalloutMemoColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(CALLOUT_MEMO_ERROR_COLOR_ICON)]: ICON,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_ERROR_COLOR_TEXT)]: TEXT,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_ERROR_COLOR_BG)]: BG,
    };
};

export const getCalloutMemoInfoTheme = (colors = {}, config = {}) => {
    const { sourceColors } = colors;
    const { ICON, TEXT, BG } = getCalloutMemoColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(CALLOUT_MEMO_INFO_COLOR_ICON)]: ICON,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_INFO_COLOR_TEXT)]: TEXT,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_INFO_COLOR_BG)]: BG,
    };
};

export const getCalloutMemoDefaultTheme = (colors = {}, config = {}) => {
    const { canvasColor = {} } = colors;
    const { isDarkMode } = config;
    const bgLightness = isDarkMode ? 20 : 80;
    const bgColor = getNewColorByChangingLightness(
        canvasColor.hex,
        bgLightness,
    );
    const textColor = getTextColor(bgColor);

    return {
        [getThemeAPIKeyFromName(CALLOUT_MEMO_COLOR_BG)]: bgColor,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_COLOR_ICON)]: textColor,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_COLOR_TEXT)]: textColor,
    };
};

const getCalloutTheme = (TOKEN_ID, colors = {}, config = {}) => {
    const { sourceColors = {} } = colors;
    const { shouldAutoCorrectColors = true } = config;
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;
    const themeData = {
        [getThemeAPIKeyFromName(TOKEN_ID)]: shouldAutoCorrectColors
            ? recommendedNonTextColor.hex
            : base.hex,
    };

    return themeData;
};

export const getCalloutDefaultTheme = (colors = {}, config = {}) => {
    return getCalloutTheme(CALLOUT_COLOR_ICON, colors, config);
};

export const getCalloutAffirmativeTheme = (colors = {}, config = {}) => {
    return getCalloutTheme(CALLOUT_AFFIRMATIVE_COLOR_ICON, colors, config);
};

export const getCalloutCautionaryTheme = (colors = {}, config = {}) => {
    return getCalloutTheme(CALLOUT_CAUTIONARY_COLOR_ICON, colors, config);
};

export const getCalloutErrorTheme = (colors = {}, config = {}) => {
    return getCalloutTheme(CALLOUT_ERROR_COLOR_ICON, colors, config);
};

export const getCalloutInfoTheme = (colors = {}, config = {}) => {
    return getCalloutTheme(CALLOUT_INFO_COLOR_ICON, colors, config);
};
