/* eslint-disable import/prefer-default-export */
import { getThemeAPIKeyFromName } from '../dataUtils';
import { NAV_COLOR_TOKEN_NAMES } from '../../const/tokens/navTokens';

const { NAV_ACTION_ICON_COLOR } = NAV_COLOR_TOKEN_NAMES;

export const getNavTheme = (colors = {}) => {
    const { neutralColor = {} } = colors;
    const tokens = {
        [getThemeAPIKeyFromName(NAV_ACTION_ICON_COLOR)]: neutralColor.hex,
    };

    return tokens;
};
