/* eslint-disable import/prefer-default-export */
import {
    getNewColorByChangingLightness,
    getRecommendedColor,
    convertColorToRGBA,
    genColorsData,
} from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getThemeAPIKeyFromName } from '../dataUtils';
import { getCorrectedLightnessAdjustment } from './themeUtils';
import { TABS_COLOR_TOKEN_NAMES } from '../../const/tokens/tabsTokens';

const {
    TABS_ACTIVE_ACCENT_COLOR,
    TABS_ACTIVE_COLOR_BG,
    TABS_ACTIVE_FOCUS_HALO_COLOR,
    TABS_ACTIVE_COLOR_TEXT,
    TABS_COLOR_BORDER,
    TABS_INACTIVE_COLOR_TEXT,
    TABS_INACTIVE_COLOR_BG,
    TABS_INACTIVE_HOVER_COLOR_TEXT,
    TABS_INACTIVE_HOVER_COLOR_BG,
    TABS_PANEL_COLOR_BG,
} = TABS_COLOR_TOKEN_NAMES;

export const getTabsTheme = (colors = {}, config = {}) => {
    const {
        sourceColors = {},
        canvasColors = {},
        canvasAltColors = {},
        universalBorderColor = {},
    } = colors;
    const { shouldAutoCorrectColors = true, isDarkMode, isMobile } = config;
    const { base = {}, recommendedTextColor = {} } = sourceColors;
    const recommendedAccentColor = shouldAutoCorrectColors
        ? recommendedTextColor
        : base;
    const recommendedInactiveTextColor = getRecommendedColor(
        canvasAltColors.base,
        canvasAltColors.variants,
        true,
    );
    const inactiveHoverLightness = getCorrectedLightnessAdjustment(
        canvasAltColors?.base?.lightness,
        10,
        isDarkMode,
    );
    const inactiveHoverBGColor = genColorsData({
        hex: getNewColorByChangingLightness(
            canvasAltColors?.base?.hex,
            inactiveHoverLightness,
        ),
    });
    const recommendedInactiveHoverTextColor = getRecommendedColor(
        inactiveHoverBGColor?.base,
        inactiveHoverBGColor?.variants,
        true,
    );

    return {
        [getThemeAPIKeyFromName(TABS_ACTIVE_COLOR_BG)]: canvasColors?.base?.hex,
        [getThemeAPIKeyFromName(TABS_PANEL_COLOR_BG)]: canvasColors?.base?.hex,
        [getThemeAPIKeyFromName(TABS_ACTIVE_COLOR_TEXT)]:
            recommendedAccentColor?.hex,
        [getThemeAPIKeyFromName(TABS_ACTIVE_ACCENT_COLOR)]:
            recommendedAccentColor?.hex,
        [getThemeAPIKeyFromName(TABS_INACTIVE_COLOR_TEXT)]:
            recommendedInactiveTextColor?.hex,
        [getThemeAPIKeyFromName(TABS_INACTIVE_COLOR_BG)]:
            canvasAltColors?.base?.hex,
        [getThemeAPIKeyFromName(TABS_COLOR_BORDER)]: universalBorderColor?.hex,
        ...(!isMobile && {
            [getThemeAPIKeyFromName(TABS_ACTIVE_FOCUS_HALO_COLOR)]:
                convertColorToRGBA(recommendedAccentColor?.hex, 0.4, true),
            [getThemeAPIKeyFromName(TABS_INACTIVE_HOVER_COLOR_TEXT)]:
                recommendedInactiveHoverTextColor?.hex,
            [getThemeAPIKeyFromName(TABS_INACTIVE_HOVER_COLOR_BG)]:
                inactiveHoverBGColor?.base?.hex,
        }),
    };
};
