import React from 'react';
import ReactDOM from 'react-dom/client';

import ChatModule from './ChatModule';

const renderChat = (element, config) => {
    const root = ReactDOM.createRoot(element);
    root.render(
        <React.StrictMode>
            <ChatModule {...config} />
        </React.StrictMode>,
    );
};

export default renderChat;
