import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import ButtonIcon from './components/ButtonIcon';

// Styles
import './styles/dialog-action.scss';

class DialogAction extends PureComponent {
    static defaultProps = {
        isDisabled: false,
        onClick: () => Promise.resolve(),
    };

    static propTypes = {
        'aria-label': PropTypes.string.isRequired,
        contextCls: PropTypes.string,
        isDisabled: PropTypes.bool,
        onClick: PropTypes.func,
        renderIcon: PropTypes.func,
    };

    render() {
        const { contextCls, children, renderIcon, onClick } = this.props;
        const props = this.getExtantProps();

        return (
            <button
                {...props}
                className={cx(this.baseCls, contextCls)}
                type="button"
                onClick={onClick}
            >
                <span className={`${this.baseCls}__content-container`}>
                    {!children && renderIcon && this.renderIcon()}
                    {children}
                </span>
            </button>
        );
    }

    renderIcon = () => {
        const { renderIcon } = this.props;

        return <ButtonIcon baseCls={this.baseCls} renderIcon={renderIcon} />;
    };

    getExtantProps = () => {
        const {
            'aria-label': ariaLabel,
            contextCls,
            isDisabled,
            onClick,
            renderIcon,
            children,
            ...rest
        } = this.props;

        return {
            ...rest,
            ...(ariaLabel && { 'aria-label': ariaLabel }),
            disabled: isDisabled,
        };
    };

    baseCls = 'eureka-dialog-action';
}

export default DialogAction;
