/* eslint-disable import/prefer-default-export */
import { getThemeAPIKeyFromName } from '../dataUtils';
import { TRANSACTIONS_TOKEN_NAMES } from '../../const/tokens/transactionsTokens';

const {
    TRANSACTIONS_CATEGORY_COLOR_ICON,
    TRANSACTIONS_NEGATIVE_COLOR_TEXT,
    TRANSACTIONS_OMMITTED_COLOR_TEXT,
    TRANSACTIONS_POSITIVE_COLOR_TEXT,
} = TRANSACTIONS_TOKEN_NAMES;

export const getTransactionsTheme = (colors = {}, config = {}) => {
    const {
        affirmativeColors = {},
        errorColors = {},
        textAltColor = {},
        iconColor = {},
    } = colors;
    const { shouldAutoCorrectColors = true } = config;
    const {
        base: baseAffirmativeColor = {},
        recommendedTextColor: recommendedAffirmativeTextColor = {},
    } = affirmativeColors;
    const {
        base: baseErrorColor = {},
        recommendedTextColor: recommendedErrorTextColor = {},
    } = errorColors;
    const themeData = {
        [getThemeAPIKeyFromName(TRANSACTIONS_CATEGORY_COLOR_ICON)]:
            iconColor.hex,
        [getThemeAPIKeyFromName(TRANSACTIONS_NEGATIVE_COLOR_TEXT)]:
            shouldAutoCorrectColors
                ? recommendedErrorTextColor.hex
                : baseErrorColor.hex,
        [getThemeAPIKeyFromName(TRANSACTIONS_OMMITTED_COLOR_TEXT)]:
            textAltColor.hex,
        [getThemeAPIKeyFromName(TRANSACTIONS_POSITIVE_COLOR_TEXT)]:
            shouldAutoCorrectColors
                ? recommendedAffirmativeTextColor.hex
                : baseAffirmativeColor.hex,
    };

    return themeData;
};
