import { getNewColorByChangingLightness } from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getTextColor } from './themeUtils';
import { getThemeAPIKeyFromName } from '../dataUtils';
import { BADGE_TOKEN_NAMES } from '../../const/tokens/badgeTokens';
import { BADGE_STYLE_TOKEN_DEFAULTS } from '../../const/token-defaults/badgeTokenDefaults';

const {
    BADGE_BORDER_RADIUS,
    BADGE_COLOR_BG,
    BADGE_COLOR_TEXT,
    BADGE_AFFIRMATIVE_COLOR_BG,
    BADGE_AFFIRMATIVE_COLOR_TEXT,
    BADGE_CAUTIONARY_COLOR_BG,
    BADGE_CAUTIONARY_COLOR_TEXT,
    BADGE_ERROR_COLOR_BG,
    BADGE_ERROR_COLOR_TEXT,
    BADGE_INFO_COLOR_BG,
    BADGE_INFO_COLOR_TEXT,
} = BADGE_TOKEN_NAMES;

const getBadgeColors = (sourceColors = {}, config = {}) => {
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;
    const { shouldAutoCorrectColors } = config;
    const BG = shouldAutoCorrectColors ? recommendedNonTextColor.hex : base.hex;

    return {
        BG,
        TEXT: getTextColor(BG),
    };
};

export const getBadgeStyle = (data = {}) => {
    const apiKeyRadius = getThemeAPIKeyFromName(BADGE_BORDER_RADIUS);
    const defaultRadius = BADGE_STYLE_TOKEN_DEFAULTS[BADGE_BORDER_RADIUS];
    const { [apiKeyRadius]: radius = defaultRadius } = data;

    return {
        [apiKeyRadius]: radius,
    };
};

export const getBadgeAffirmativeTheme = (sourceColors = {}, config = {}) => {
    const { BG, TEXT } = getBadgeColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(BADGE_AFFIRMATIVE_COLOR_BG)]: BG,
        [getThemeAPIKeyFromName(BADGE_AFFIRMATIVE_COLOR_TEXT)]: TEXT,
    };
};

export const getBadgeCautionaryTheme = (sourceColors = {}, config = {}) => {
    const { BG, TEXT } = getBadgeColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(BADGE_CAUTIONARY_COLOR_BG)]: BG,
        [getThemeAPIKeyFromName(BADGE_CAUTIONARY_COLOR_TEXT)]: TEXT,
    };
};

export const getBadgeErrorTheme = (sourceColors = {}, config = {}) => {
    const { BG, TEXT } = getBadgeColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(BADGE_ERROR_COLOR_BG)]: BG,
        [getThemeAPIKeyFromName(BADGE_ERROR_COLOR_TEXT)]: TEXT,
    };
};

export const getBadgeInfoTheme = (sourceColors = {}, config = {}) => {
    const { BG, TEXT } = getBadgeColors(sourceColors, config);

    return {
        [getThemeAPIKeyFromName(BADGE_INFO_COLOR_BG)]: BG,
        [getThemeAPIKeyFromName(BADGE_INFO_COLOR_TEXT)]: TEXT,
    };
};

export const getBadgeDefaultTheme = (canvasColor = {}, config = {}) => {
    const { isDarkMode } = config;
    const bgLightness = isDarkMode ? 20 : 80;
    const bgColor = getNewColorByChangingLightness(
        canvasColor.hex,
        bgLightness,
    );
    const textColor = getTextColor(bgColor);

    return {
        [getThemeAPIKeyFromName(BADGE_COLOR_BG)]: bgColor,
        [getThemeAPIKeyFromName(BADGE_COLOR_TEXT)]: textColor,
    };
};
