/* eslint-disable import/prefer-default-export */
import { getTextColor } from './themeUtils';
import { getThemeAPIKeyFromName } from '../dataUtils';
import { PROGRESS_TRACKER_COLOR_TOKEN_NAMES } from '../../const/tokens/progressTrackerTokens';

const {
    PROGRESS_TRACKER_STEP_COLOR_BG,
    PROGRESS_TRACKER_STEP_COLOR_BORDER,
    PROGRESS_TRACKER_STEP_COLOR_TEXT,
    PROGRESS_TRACKER_STEP_COMPLETE_COLOR_BG,
    PROGRESS_TRACKER_STEP_COMPLETE_COLOR_BORDER,
    PROGRESS_TRACKER_STEP_COMPLETE_COLOR_TEXT,
} = PROGRESS_TRACKER_COLOR_TOKEN_NAMES;

export const getProgressTrackerTheme = (colors = {}, config = {}) => {
    const { sourceColors = {}, neutralColor = {}, canvasColor = {} } = colors;
    const { shouldAutoCorrectColors = true } = config;
    const { base = {}, recommendedNonTextColor = {} } = sourceColors;
    const completeColor = shouldAutoCorrectColors
        ? recommendedNonTextColor.hex
        : base.hex;
    const tokens = {
        [getThemeAPIKeyFromName(PROGRESS_TRACKER_STEP_COLOR_BG)]:
            canvasColor.hex,
        [getThemeAPIKeyFromName(PROGRESS_TRACKER_STEP_COLOR_BORDER)]:
            neutralColor.hex,
        [getThemeAPIKeyFromName(PROGRESS_TRACKER_STEP_COLOR_TEXT)]:
            neutralColor.hex,
        [getThemeAPIKeyFromName(PROGRESS_TRACKER_STEP_COMPLETE_COLOR_BG)]:
            completeColor,
        [getThemeAPIKeyFromName(PROGRESS_TRACKER_STEP_COMPLETE_COLOR_BORDER)]:
            completeColor,
        [getThemeAPIKeyFromName(PROGRESS_TRACKER_STEP_COMPLETE_COLOR_TEXT)]:
            getTextColor(completeColor),
    };

    return tokens;
};
